
#app {
  position: relative;
  min-height: 100vh;
  width: 100%;
  background: #f4f4f6;
}
#app .color-b {
  color: #2e2c6b;
}
#app .my-dialog {
  position: absolute;
}
#app .my-personindex .mu-icon.material-icons {
  color: #2e2c6b;
}
#app .container-top {
  position: fixed;
  z-index: 101;
  top: 0;
  left: 0;
  width: 100%;
  height: 10%;
}
#app .container-top .drawer {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 6vw;
  height: 100%;
}
#app .container-bottom {
  position: fixed;
  z-index: 1;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 10%;
}
#app .container-content {
  width: 100%;
}
#app .container-content .patch {
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 10.2vh;
}
#app .container-content .t-1 {
  color: #2e2c6b;
}
#app .container-content .mu-tab-text.has-icon {
  margin-top: -4px;
}
#app .container-content .i-1 {
  font-size: 2.5em;
  color: #64dd17;
}
#app .container-content .i-2 {
  font-size: 2.5em;
  color: #f44336;
}
#app .container-content .i-3 {
  font-size: 2.5em;
  color: #00bfa5;
}
#app .container-content .ii-1 {
  font-size: 2.5em;
  color: #ffd600;
}
#app .container-content .ii-2 {
  font-size: 2.5em;
  color: #ec407a;
}
#app .container-content .ii-3 {
  font-size: 2.5em;
  color: #2962ff;
}


