
.icons {
  color: #f00;
}
.index {
  position: absolute;
  z-index: 102;
  top: 0;
  left: 0;
  width: 100%;
  background: #f4f4f6;
}
.index .top {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 103;
  width: 100%;
  height: 10%;
}
.index .top .mu-appbar {
  background: #fff;
  color: #333;
  text-align: center;
}
.index .bottom {
  position: fixed;
  left: 0;
  bottom: 0;
  z-index: 103;
  width: 100%;
  height: 10%;
  background: #fff;
}
.index .content {
  width: 100%;
  margin-bottom: 56px;
  background-size: cover;
}
.index .content .person-bg {
  width: 100%;
  height: 200px;
}
.index .content .person-info {
  margin-top: -48px;
  text-align: center;
}
.index .content .person-info .name {
  display: block;
  margin-top: 4px;
  font-size: 1.6em;
  color: #333;
}
.index .content .mu-tabs {
  background: #999;
}
.index .content .item {
  margin-top: 6px;
  margin-left: 20px;
  margin-bottom: 56px;
}


