
.mu-list {
  overflow: hidden;
  background: #f4f4f6;
}
.mu-list .swipeleft {
  -webkit-transform: translateX(-45%);
          transform: translateX(-45%);
}
.mu-list .wrap {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 200%;
  overflow: hidden;
  transition: all 0.3s linear;
  position: relative;
}
.mu-list .wrap:after {
  display: block;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0;
  border-top: 1px solid rgba(0,0,0,0.1);
  content: '';
}
.mu-list .wrap .list-item {
  width: 50%;
  background: #fff;
  transition: all 0.3s linear;
}
.mu-list .wrap .ms-item {
  width: 15%;
  padding: 1.5em 0;
  font-size: 1.2em;
  font-weight: 500;
  text-align: center;
  color: #fff;
}
.mu-list .wrap .post-top {
  background: #bdbdbd;
}
.mu-list .wrap .sign-read {
  background: #64dd17;
}
.mu-list .wrap .delete {
  background: #ff1744;
}
.mu-list .item-right {
  position: relative;
}
.mu-list .item-right .time {
  position: absolute;
  top: -10px;
  left: -16px;
  display: inline-block;
}
.mu-list .item-right .mu-badge {
  position: absolute;
  top: 0;
  left: 50%;
  width: auto;
  border-radius: 8px;
  min-width: 16px;
  height: 16px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
}


